const eventConfig = {
  PAGE_VIEW:'PAGE_VIEW',
  SELECT_CONTENT:'SELECT_CONTENT',
  BEGIN_CHECKOUT:'BEGIN_CHECKOUT',
  ECOMMERCE_PURCHASE:'ECOMMERCE_PURCHASE',
  OPEN_INVOICE_PAGE:'openInvoicePage',
  PAY_INVOICE:'payInvoice'
};

const logEvent = function(name, params) {
  if (!name) {
    return;
  }

  if (window.AnalyticsWebInterface) {
    // Call Android interface
    window.AnalyticsWebInterface.webEvent(name, JSON.stringify(params));
  } else if (window.webkit
    && window.webkit.messageHandlers
    && window.webkit.messageHandlers.firebase) {
    // Call iOS interface
    var message = {
      command: 'logEvent',
      name: name,
      parameters: params
    };
    window.webkit.messageHandlers.firebase.postMessage(message);
  } else {
    // No Android or iOS interface found
    console.log("No native APIs found.");
  }
};

export default {eventConfig, logEvent};
