import Vue from 'vue'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)

const DEFAULT_LANG = 'zh-TW'
export const LOCALE_KEY = 'localeLanguage'

const locales = {
  // 'en-US': require('./locales/en.json'),
  // 'zh-CN': require('./locales/zh-Hans.json'),
  'zh-TW': require('./locales/zh-Hant.json'),
}

const i18n = new VueI18n({
  locale: DEFAULT_LANG,
  messages: locales,
})

Vue.prototype.$setupLang = function setupLang(lang) {
  if (lang === undefined) {
    lang = window.localStorage.getItem(LOCALE_KEY)
    if (locales[lang] === undefined) {
      lang = DEFAULT_LANG
    }
  }
  window.localStorage.setItem(LOCALE_KEY, lang)

  Object.keys(locales).forEach(lang => {
    document.body.classList.remove(`lang-${lang}`)
  })
  document.body.classList.add(`lang-${lang}`)
  document.body.setAttribute('lang', lang)

  Vue.config.lang = lang
  i18n.locale = lang
  // update the lang to router
  this.$router.push({name: this.$route.name, params: {lang: lang}, query: this.$route.query})
}
export default i18n
