<script>
export default {
  name: 'app',
  data() {
    return {
      token: '',
    };
  },
  created() {
    const searchs = window.location.search.slice(1).split('&')
    let aff = searchs.find(item => {
      return item.indexOf("aff=") > -1
    })

    if (!!aff) {
      localStorage.setItem('aff', aff.match(/aff=(.*)/)[1]);
    }

    let inapp = searchs.find(item => {
      return item.indexOf("inapp=") > -1
    })
    if (!!inapp) {
      localStorage.setItem('inapp', inapp.match(/inapp=(.*)/)[1]);
    }


    let email = searchs.find(item => {
      return item.indexOf("email=") > -1
    })
    if (!!email) {
      localStorage.setItem('email', email.match(/email=(.*)/)[1]);
    }


    let token = searchs.find(item => {
      return item.indexOf("token=") > -1
    })
    if (!!token) {
      // the url is url-encoded, so we need reverse it, or the token isn't correct
      const decoded = decodeURIComponent(token.match(/token=(.*)/)[1]);
      // persist token between reloads
      localStorage.setItem('token', decoded);
      // remove token in url right after we have it
      //window.location.href = `${window.location.origin}${window.location.pathname}`;
    }

    if (!!inapp || !!token) {
       window.location.href = `${window.location.origin}${window.location.pathname}`;
    }


