    // this.$request.eventTracer()
  },
  mounted() {
    window.zE(function() {
      if (!!localStorage.getItem("email")) {
        window.zE.identify({
          name: localStorage.getItem("email")
                  .split('@')[0],
          email: localStorage.getItem("email"),
        });
      }
      window.zE.setLocale('zh-TW');
    });
