import axios from 'axios';
import { LOCALE_KEY } from './i18n';

const baseURL = process.env.VUE_APP_SERVER_URL;

const instance = axios.create({
  baseURL,
  timeout: 30000,
  withCredentials: false,
  headers: {'Accept-Language': getLang(window.localStorage.getItem(LOCALE_KEY))}
});

instance.interceptors.response.use(response => response, (e) => {
  const { response } = e;
  if (response == null) {
     return null
  }
  if (response.status === 401 && response.data.redirect_uri !== undefined) {
    window.location.href = response.data.redirect_uri;
    window.location.reload(true);
  } else if (response.status === 403) {
    localStorage.removeItem('token')
    window.location.href = window.location.origin;
    //window.location.reload(true);
  } else {
    // return Promise.reject(response.data)
    return response.data
  }
});

instance.interceptors.request.use((config) => {
  const token = localStorage.getItem('token');
  // If it's Zendesk request, do not add the token
  if (token && config.url.indexOf('help_center') === -1) {
    config.headers.common.Authorization = `Bearer ${token}`;
  } else {
    config.headers.common.Authorization = ''
  }
  // config.headers.common['Accept-Language'] = window.localStorage.getItem(LOCALE_KEY)
  return config;
}, error => console.error(error));

function getLang(lang) {
  let serviceLang = ''
  switch (lang) {
    case 'zh-TW':
      serviceLang = 'zh-Hant'
      break;
    case 'zh-CN':
      serviceLang = 'zh-Hans'
      break;
    case 'en-US':
      serviceLang = 'en'
      break;
    default:
      serviceLang = 'zh-Hant'
      break;
  }
  return serviceLang
}

export default instance;
