import Vue from 'vue';
import Router from 'vue-router';
import { LOCALE_KEY } from './i18n';

const authChecker = (to, from, next) => {
  if (localStorage.getItem('inapp') === null && localStorage.getItem('token') === null){
  //if (localStorage.getItem('token') === null) {
    next(`error-need-login`);
  }
  next();
};
Vue.use(Router);

const router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/:lang',
      name: 'root',
      // redirect: `/:lang/home`,
      component: () => import('./App'),
      children: [
        {
          path: 'home',
          name: 'home',
          component: () => import('./views/Home'),
        },
        // {
        //   path: 'svgtest',
        //   name: 'svgtest',
        //   component: () => import('./views/SvgTest'),
        // },
        {
          path: 'invoices',
          redirect: 'my/invoices'
        },
        // {
        //   path: 'login',
        //   name: 'login',
        //   component: () => import('./views/Login'),
        // },
        {
          path: 'order',
          redirect: 'order/place-order',
        },
        {
          path: 'order/place-order',
          name: 'pay-order',
          component: () => import('./views/Product.vue'),
        },
        {
          path: 'order/confirm-order',
          name: 'pay-success',
          component: () => import('./views/PaySuccess.vue'),
        },
        {
          path: 'order/generate-order',
          name: 'interchange',
          component: () => import('./views/Interchange.vue'),
        },
        {
          path: 'order/viewInvoice',
          name: 'viewInvoice',
          component: () => import('./views/ViewInvoice.vue'),
        },
        {
          path: 'my',
          redirect: 'my/service',
          component: () => import('./views/My'),
          beforeEnter: authChecker,
          children: [
            {
              path: 'invoices',
              name: 'invoice',
              component: () => import('./views/Invoice'),
            },
            {
              path: 'products',
              name: 'product',
              component: () => import('./views/Product'),
            },
            {
              path: 'service',
              name: 'service',
              component: () => import('./views/UsersDashboard'),
            },
          ],
        },
        {
          path: 'users',
          redirect: 'users/signin',
          component: () => import('./views/UsersBase'),
          children: [
            {
              path: 'signup',
              name: 'users-signup',
              component: () => import('./views/Signup'),
            },
            {
              path: 'forget-password',
              name: 'forget-password',
              component: () => import('./views/ForgetPassword')
            },
            {
              path: 'reset-password/:code',
              name: 'reset-password',
              component: () => import('./views/ResetPassword')
            },
            {
              path: 'change-password',
              name: 'change-password',
              component: () => import('./views/ChangePassword')
            },
            {
              path: 'signup-success',
              name: 'signup-success',
              component: () => import('./views/SignupSuccess'),
            },
            {
              path: 'signup-feedback',
              component: () => import('./views/SignupFeedback'),
            },
            {
              path: 'signin',
              name: 'signin',
              component: () => import('./views/Signin'),
            },
          ],
        },
        {
          path: 'invoices/:id',
          name: 'invoices-detail',
          beforeEnter: authChecker,
          component: () => import('./views/InvoiceDetail')
        },
        {
          path: 'activities',
          name: 'activity-center',
          component: () => import(/* webpackChunkName: "ActivityCenter" */ './views/ActivityCenter.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'rewards',
          name: 'reward',
          component: () => import(/* webpackChunkName: "Reward" */ './views/Reward.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'invitations',
          name: 'invite-list',
          component: () => import(/* webpackChunkName: "InviteList" */ './views/InviteList.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'commissions',
          name: 'commission-list',
          component: () => import(/* webpackChunkName: "CommissionList" */ './views/CommissionList.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'invite',
          name: 'invite-friend',
          component: () => import(/* webpackChunkName: "InviteFriend" */ './views/InviteFriend.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'invite/:code',
          name: 'invite-code',
          component: () => import(/* webpackChunkName: "InviteCode" */ './views/InviteCode.vue'),
        },
        {
          path: 'products',
          name: 'products',
          component: () => import( './views/Product'),
          beforeEnter: (to, from, next) => {
            if (to.query.token) {
              next(`${to.params.lang}/my/products?token=${to.query.token}`);
            } else {
              next(`${to.params.lang}/my/products`);
            }
          },
        },
        {
          path: 'bug-report',
          name: 'bug-report',
          component: () => import(/* webpackChunkName: "BugReport" */ './views/BugReport.vue'),
          beforeEnter: authChecker,
        },
        {
          path: 'error-need-login',
          name: 'need-login',
          component: () => import(/* webpackChunkName: "NeedLogin" */ './views/NeedLogin.vue'),
        },
        {
          path: 'help-center',
          name: 'support',
          component: () => import(/* webpackChunkName: "Support" */ './views/Support.vue'),
          children: [
            {
              path: 'article/:articleId',
              name: 'article',
              component: () => import(/* webpackChunkName: "SupportArticle" */ './components/SupportArticle.vue')
            },
            {
              path: 'contact-us',
              name: 'contact-us',
              component: () => import(/* webpackChunkName: "SupportContactUsView" */ './components/SupportContactUsView.vue')
            }
          ]
        },
        {
          path: '*',
          name: '404',
          component: () => import(/* webpackChunkName: "404" */ './views/404.vue'),
        },
      ]
    },

  ],
});
router.beforeEach((to, from, next) => {
  // init or reload the lang
  // const test = to
  // const testdata = to.params
  // const testlang = to.params.lang
  // const testfrom = from
  // console.log('--------------------------------------------------------')
  // console.log(testfrom)
  // console.log(test)
  // console.log(testdata)
  // console.log(testlang)
  if (to.params.lang !== undefined) {
    // console.log('if - 1')
    switch (to.params.lang) {
      case 'en-US':
      //   // set lang to local
      //   window.localStorage.setItem(LOCALE_KEY, 'en-US');
      //   break;
      case 'zh-CN':
      //   window.localStorage.setItem(LOCALE_KEY, 'zh-CN');
      //   break;
      case 'zh-TW':
        window.localStorage.setItem(LOCALE_KEY, 'zh-TW');
        break;
      default :
        // if user access a exit router but missing lang, then will add the lang
        // console.log(to.path.substr(1))
        // console.log(router.options)
        let exitRouter = router.options.routes[0].children.find(value => {
          return value.path === to.path.substr(1);
        });
        // console.log(exitRouter)
        // if (exitRouter) {
        //   if (window.localStorage.getItem(LOCALE_KEY) !== null) {
        //     next(`${window.localStorage.getItem(LOCALE_KEY)}${to.path}`);
        //   } else {
        //     next(`zh-TW/${to.path}`);
        //   }
        //   return;
        // }
        if (window.localStorage.getItem(LOCALE_KEY) !== null) {

          next({ path: `${window.localStorage.getItem(LOCALE_KEY)}${to.path}`,
            params: {lang: window.localStorage.getItem(LOCALE_KEY)},
            query: to.query});
        } else {

          window.localStorage.setItem(LOCALE_KEY, 'zh-TW')
          // Router will add lang first, need to replace the path ot redirect if it's exist
          if (!!exitRouter && !!exitRouter.redirect && exitRouter.redirect !== to.path) {
            next({
              path: `zh-TW/${exitRouter.redirect}`,
              params: {lang: 'zh-TW'},
              query: to.query
            });
          } else {
            next({
              path: `zh-TW/${to.path}`,
              params: {lang: 'zh-TW'},
              query: to.query
            });
          }
        }

        // if no exit lang, set en-US as default, and go to home page
        // window.localStorage.setItem(LOCALE_KEY, 'en-US');
        // to.params.lang = 'en-US';
        // next('en-US/home');
        // window.localStorage.setItem(LOCALE_KEY, 'zh-TW');
        // to.params.lang = 'zh-TW';
        // next('zh-TW/home');

        return;
    }
    // if in root, redirect to home page
    if (to.name === 'root') {
      // console.log('root')
      // next(`${to.params.lang}/home`);
      next({
        path: `${window.localStorage.getItem(LOCALE_KEY)}/home`,
        params: {lang: window.localStorage.getItem(LOCALE_KEY)},
        query: to.query,
        name: 'home'
      })
    }
  } else {
    if (window.localStorage.getItem(LOCALE_KEY) === null) {
      // console.log('if - 2')

      // init lang and go to home page
      // window.localStorage.setItem(LOCALE_KEY, 'en-US');
      // to.params.lang = 'en-US';
      // next('en-US/home');
      window.localStorage.setItem(LOCALE_KEY, 'zh-TW');
      to.params.lang = 'zh-TW';
      next('zh-TW/home');
    } else {
      // console.log('if - 3')
      // console.log(to)
      // console.log(window.localStorage.getItem(LOCALE_KEY))
      next({
        path: `${window.localStorage.getItem(LOCALE_KEY)}/home`,
        params: {lang: window.localStorage.getItem(LOCALE_KEY)},
        name: 'home'
      });
    }
    return;
  }
  next();
});



