// getData来源于子组件的function，为的是减少代码量，提高复用。
// 3s内检测到token才可以发送http请求（比App.vue多一次，为了防止边界情况）
Vue.prototype.$getAsyncData = function getAsyncData(getData) {
  if (location.href.indexOf('quick-buy') > -1) {
    getData()
  } else {
    let token = localStorage.getItem('token');
    if (token) {
      getData();
    } else {
      const dt = setInterval(() => {
        token = localStorage.getItem('token');
        if (token) {
          getData();
          clearInterval(dt);
        }
      }, 1000);
      setTimeout(() => {
        clearInterval(dt);
      }, 3100);
    }
  }
};
Vue.prototype.$isPc = function() {
  let width = document.documentElement.clientWidth || document.body.clientWidth;
  return width > 500;
  //return !navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
}
Vue.prototype.firebaselogger = firebaselogger;
window.Clipboard = (function (window, document, navigator) {
  let textArea;
  let copy;

  function isOS() {
    return navigator.userAgent.match(/ipad|iphone/i);
  }

  function createTextArea(text) {
    textArea = document.createElement('textArea');
    textArea.value = text;
    document.body.appendChild(textArea);
  }

  function selectText() {
    let range;
    let selection;

    if (isOS()) {
      range = document.createRange();
      range.selectNodeContents(textArea);
      selection = window.getSelection();
      selection.removeAllRanges();
      selection.addRange(range);
      textArea.setSelectionRange(0, 999999);
    } else {
      textArea.select();
    }
  }

  function copyToClipboard() {
    document.execCommand('copy');
    document.body.removeChild(textArea);
  }

  copy = function (text) {
    createTextArea(text);
    selectText();
    copyToClipboard();
  };

  return {
    copy,
  };
}(window, document, navigator));
