import axios from './axios';

let host = `https://sg.inforun.work`
let baseUrl = `${host}/api/v2/help_center`;
let _categories = 'categories.json';
let _sections = 'sections.json';
let _articles = 'articles.json';
let language = 'zh-tw' //window.localStorage.getItem('localeLanguage')
// let bashUrlApi = 'https://fatpipe.work/api/'

const instance = {
  tracerFlag: false,
  /**
   * Get the user's invoices
   * @param params Can be null
   * @param callback Handle the response
   */
  getUsersInvoices: function (params, callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/invoices`, { params })
      .then(data => callback(data))
  },

  getZendeskCategories: function(callback) {
    axios.get(`${baseUrl}/${language}/${_categories}`)
      .then(data => callback(data))
  },

  getZendeskSections: function(callback) {
    axios.get(`${baseUrl}/${language}/${_sections}`)
      .then(data => callback(data))
  },

  getZendeskArticles: function(callback) {
    axios.get(`${baseUrl}/${language}/${_articles}?per_page=90`)
      .then(data => callback(data))
  },

  getZendeskArticle: function(articleId, callback) {
    axios.get(`${baseUrl}/${language}/articles/${articleId}.json`)
      .then(data => callback(data))
  },

  getSearchKeyword: function(keyword, callback) {
    axios.get(`${baseUrl}/articles/search.json?query=${keyword}`)
      .then(data => callback(data))
  },

  postNewRequest(params, callback) {
    axios.post(`${host}/api/v2/requests.json`, params)
      .then(data => callback(data))
  },

  /**
   * Get the invoice detail
   * @param invoiceId The invoice ID which you want to get
   * @param callback Handle the response
   */
  getUsersInvoiceDetail: function (invoiceId, callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/invoices/${invoiceId}`)
      .then(data => callback(data))
  },

  /**
   * Verify quick buy, get the token and invoice id which for checking the invoice's status
   * @param decoded
   * @param params
   * @param callback
   */
  getVerifyQuickBuy: function (decoded, params, callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders/quickbuy/${decoded}`, { params })
      .then(data => callback(data))
  },

  /**
   * Get user's services - list
   * @param callback
   */
  getAllServices(callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/services`)
        .then(data => callback(data))
  },

  /**
   * Get more detail of a service
   * @param serviceId
   * @param callback
   */
  getServiceDetail(serviceId, callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v1/services/${serviceId}`)
        .then(data => callback(data))
  },

  /**
   * change password
   * @param params
   * @param callback
   */
  postChangePassword(params, callback) {
    axios.post(`${process.env.VUE_APP_SERVER_URL}/api/v2/users/changepassword`, params)
      .then(data => callback(data))
  },

  /**
   * EmailCheck
   * @param params
   * @param callback
   */
  emailCheck(params, callback) {
    axios.get(`${process.env.VUE_APP_SERVER_URL}/api/v2/users/emailcheck?email=${params}`)
      .then(data => callback(data))
  },

  /**
   * Logged in，order
   * @param params
   * @param callback
   */
  postOrders(params, callback) {
    axios.post(`${process.env.VUE_APP_SERVER_URL}/api/v1/orders`, params)
      .then(data => callback(data))
  },

  /**
   * Get the URL for auto login with WHMCS
   * @param params
   * @param callback
   */
  getAutoLoginWHMCS(params, callback) {
    axios.post(`${process.env.VUE_APP_SERVER_URL}/api/v2/users/loginWHMCS`, params)
    // axios.post(`http://localhost:3000/api/v2/users/loginWHMCS`, params)
        .then(data => callback(data))
  },


  eventTracer: function () {
    if (!this.tracerFlag) {
      this.tracerFlag = true
      console.log('event tracer on...')
    }
  }
}

export default instance
